import { useEffect, useState } from 'react'

import { default as exportFromJSON} from 'export-from-json'
import { regularizeStatus, getFeedback } from "@meddbriefer/analysis"
import { leafScoringList } from '@meddbriefer/feedback-data'

const EXPORT_TYPE = "xls"

const intvMapping = (item) => {
  if (item.type === "heading") {
    return {"id": (item.id), "label": item.label, "type": item.type, "menuType": "intervention"}
  }
  else {
    return {"id": (item.interventionID), "label": item.label, "type": item.type, "menuType": "intervention"}
  }
}

const setTemplate = (scenData) => {
  let initialChecklist = scenData._checkListMetaData.filter(e => !["checklist-heading","checklist-prompt","decision-option"].includes(e.type))
  let checklist = initialChecklist.map(e => ({"id": e.id, "label": e.label, "type": e.type, "menuType": "checklist"}))
  let initialIntvList = (scenData.interventionMenus).map(entry => entry.items).flat()
  let intvList = initialIntvList.map(e => intvMapping(e))
  return checklist.concat(intvList)
}

const getNumericScore = (e) => {
  let event = Object.assign({}, e);
  let status = regularizeStatus(event, leafScoringList).status;
  let score = 1;
  switch (status) {
    case "good":
      score = 1;
      break;
    case "absent":
      score = 0;
      break;
    case "error":
      score = "E";
      break;
    case "misordered":
      score = "M";
      break;
    case "misorderedError":
      score = "ME";
      break;
    case "alternative":
      score = "A";
      break;
    case "optional":
      score = 1;
      break;
    case "NI":
      score = 0;
      break;
    default:
      break;
  }
  return score;
};


export default function GetScoring2({studentMetaData, obsID2analyzedLogsMap, scenariosMap}) {
  const [computed, setComputed] = useState(false)
  // console.log("studentMetaData", studentMetaData)
  // console.log("obsID2analyzedLogsMap", obsID2analyzedLogsMap)
  // console.log("scenariosMap", scenariosMap)
  const firstScenario = Object.values(scenariosMap)[0]
  const orderedActions = setTemplate(firstScenario)

  const createScoringFiles = () => {
    // iterate over each student, compute the scores and output
    // a file for them
    Object.entries(studentMetaData).forEach(([userName, userData]) => {
      // userData is an array of metaData for each of userNames scenario
      // iterate over them all and merge together
      // some of the metadata fields, analysis results, and the published
      // scenario in a format scoreByEventTemplate() is expecting
      const studentScores = userData.map((rec) => {
        // console.log("scenMetaData", rec)
        const obsLogID = rec.observerLogID;
        const analysis = obsID2analyzedLogsMap[obsLogID];
        const scen = scenariosMap[rec.publishedScenarioID];
        return {
          scenario: rec.scenario,
          student: userName,
          ...analysis,
          scenData: scen,
        };
      });
      // console.log(studentScores);
      const studentScoreData = scoreByEventTemplate(studentScores)
      // console.log(studentScoreData)
      outputStudentFile(userName, studentScoreData)
    });
  }



  const scoreByEventTemplate = (scores) => {
    let data = []
    //}console.log("In scoreEachEvent input: ", scores)
    let score
    //fill in template for each action
    orderedActions.forEach(action => {
      let entry = {}
      if (["phase", "sub-phase", "heading"].includes(action.type)){
        entry["menu level"] = action.label
        entry["label"] = ""
        entry.actionID = ""
      }
      else {
        entry["menu level"] = ""
        entry["label"] = action.label
        entry.actionID = action.id}


      scores.forEach(scoreSet => {
        let foundAction = (scoreSet.events).find(e => e.id === action.id)
        let scenAction = ""
        let protocolRel = ""
        if (action.menuType === "intervention"  && action.type !== "heading"){
          scenAction = ((scoreSet.scenData).actions).find(e => e.interventionID === action.id)
          if (scenAction){
            protocolRel = scenAction.protocolRelationship
            if (["required", "not indicated", "optional"].includes(protocolRel)){protocolRel = ""}
          }}
        let lineNum = ""
        let feedback = ""
        if (foundAction){
          //console.log("found action: ", foundAction)
          score = getNumericScore(foundAction)
          feedback = getFeedback(foundAction, scoreSet.c2FB, scoreSet.fbTemplates)
          lineNum = (foundAction.numericalID).toString() + "| "}
        else {
          if (action.menuType === "intervention" && !["heading"].includes(action.type)){score = 1}
          if (action.menuType === "checklist" && !["phase", "sub-phase", ].includes(action.type)){score = 0}
          if (["phase", "sub-phase", "heading"].includes(action.type)){score = ""}
          lineNum = ""}
        if (!["E", "ME", "M"].includes(score)){feedback = " "}
        entry["line in log/feedback " + scoreSet.scenario] = lineNum + feedback
        entry["action type " + scoreSet.scenario] = protocolRel
        entry[scoreSet.scenario] = score

      })
      data.push(entry)
    });
    return data
  }

  const outputStudentFile = (userName, data) => {
    return exportFromJSON({data: data, fileName: userName, exportType: EXPORT_TYPE})
  }



  useEffect(
    () => {
      if(!computed) {
        createScoringFiles()
        setComputed(true)
      }
    },
    // eslint-disable-next-line
    []
  )


  if (!computed){
    return (<h3>Computing scores</h3>)
  }

  return (
    (<p>Check your Downloads folder</p>)
  )

}

