import React, {useState} from "react"
import { useFirebase } from "@meddbriefer/mdb-firebase";
import GetScoring2 from "./GetScoring2";
import {analyzeEvents, saveAnalysisLog} from "@meddbriefer/analysis"
import {getObserverLog, getStudentAssignments, getPublishedScenarioObjectByID, fetchAnalysisLogsByObserverLogIDs} from "@meddbriefer/scenario-data"
import { groupByKey } from "@meddbriefer/scenario-data/utils";

const CLASS_CODE = 'pilot'
// scenarios grouped, and in the order students experience them
const PRE_TEST_SCENARIOS = ["M1CA", "B1CA"];
const TRAINING_SCENARIOS = ["B4CA", "C5CA", "SC8CP", "B5CA"];
const POST_TEST_SCENARIOS = [ "M2CA", "B7CA"];
const TESTING_SCENARIOS = PRE_TEST_SCENARIOS.concat(POST_TEST_SCENARIOS)
const ALL_SCENARIOS = PRE_TEST_SCENARIOS.concat(TRAINING_SCENARIOS).concat(POST_TEST_SCENARIOS)


export default function Welcome() {
  const {db} = useFirebase()
  // const [students, setStudents] = useState([])
  const [conditions, setConditions] = useState([])
  const [scenarios, setScenarios] = useState([])
  const [matchingStudentData, setMatchingStudentData] = useState([])
  const [matchingUsernames, setMatchingUserNames] = useState([])
  const [selectedStudents, setSelectedStudents] = useState([])
  const [studentMetaData, setStudentMetaData] = useState(null)
  const [pubScensMap, setPubScensMap] = useState(null)
  const [analyzedLogsMap, setAnalyzedLogsMap] = useState(null)

  const [run, setRun] = useState(false)

  const clearStep1State = () => {
    setMatchingStudentData([])
    setMatchingUserNames([])
  }

  const clearStep2State = () => {
    setSelectedStudents([])
    setRun(false)
  }


  const conditionsChangeHandler = (event) => {
    const {value} = event.target
    if (value === "") {
      setConditions([])
    } else {
      setConditions(value.split(","));
    }
    clearStep1State()
    clearStep2State()
  }

  const scenarioChangeHandler = (event) => {
    const {value} = event.target
    if (value === "") {
      setScenarios([])
    } else {
      setScenarios(value.split(","));
    }
    clearStep1State();
    clearStep2State();
  }

  const studentsChangeHandler = (event) => {
    const selectedOptions = Array.from(event.target.selectedOptions);
    setSelectedStudents(selectedOptions.map((opt) => opt.value));
  };

  const getClassCodeCondStudents = async (event) => {
    const results = await getStudentAssignments(db, CLASS_CODE, conditions, scenarios)
    setMatchingStudentData(results)
    const tmp = Array.from(new Set(results.map(rec => rec.userName))).sort()
    setMatchingUserNames(tmp)
  }

  const handleRun = async (event) => {
    const relevantStudentData = matchingStudentData.filter(rec => selectedStudents.includes(rec.userName))
    const uniquePubScenIDs = Array.from(new Set(relevantStudentData.map(rec => rec.publishedScenarioID)))
    const observerLogIDs = relevantStudentData.map(rec => rec.observerLogID)
    setStudentMetaData(groupByKey(relevantStudentData, "userName"))
    const pubScenariosMap = {}
    const promises = uniquePubScenIDs.map(pubScenID => getPublishedScenarioObjectByID(db, pubScenID))
    await Promise.all(promises)
      .then(results => {
        results.forEach(res => pubScenariosMap[res.id] = res)
      })
    setPubScensMap(pubScenariosMap)
    let tmp = await fetchAnalysisLogsByObserverLogIDs(db, observerLogIDs)
    // compare the list of records we requested by observerLogID to
    // the ones we recieved.  if any are missing, it's because an
    // analysisLog for that observerLogID doesn't yet exist
    // if there are indeed some missing, generate and save them,
    // and then re-call fetchAnalsysLogsByObserverLogIDs() but with
    // only the ones which didn't exist, and concatenate them to the
    // original results (I would have rather have done this
    // inside of fetchAnalysisLogsByObserverLogIDs(), but that would
    // have resulted in a circular import)
    const foundAlogs = tmp.map(rec => rec.observerLogID)
    const missingAlogs = observerLogIDs.filter(obsID => ! foundAlogs.includes(obsID))
    if (missingAlogs.length > 0) {
      console.log(`generating analyses for observerIDs: ${missingAlogs}`)
      const genData = relevantStudentData.filter(rec => missingAlogs.includes(rec.observerLogID))
      // await can only be used in async functions, and Array.forEach() isn't one, so I'm
      // doing an old-school for loop
      for (let i=0; i < genData.length; i++) {
        const rec = genData[i]
        const {observerLogID, publishedScenarioID} = rec
        const scen = pubScenariosMap[publishedScenarioID]
        const obsLog = await getObserverLog(db, observerLogID)
        const analysisData = analyzeEvents(scen, obsLog)
        // we might as well save the analysis log it so we don't have to do this again
        await saveAnalysisLog(db, obsLog, analysisData, rec.userName)
      }
      const tmp2 = await fetchAnalysisLogsByObserverLogIDs(db, missingAlogs)
      tmp = tmp.concat(tmp2)
    }
    // convert tmp (the array of analysisData) to a dictionary
    // keyd by observerLogID
    const tmpMap = {}
    tmp.forEach(rec => {
      tmpMap[rec.observerLogID] = rec
    })
    setAnalyzedLogsMap(tmpMap)
    // set to true, so that <GetScoring> component will get displayed
    // and trigger the scoring and downloading of files
    setRun(true)
  }

  return (
    <div className="container">
      <div className="row">
        <div className="col-12">
          <p className="fs-3">
            Select the scenarios and conditions you are interested in:
          </p>
        </div>
      </div>
      <div className="row">
        <div className="col-6">
          <label htmlFor="scenarios_menu" className="form-label">
            Scenarios
          </label>
          <select
            id="scenarios_menu"
            className="form-control"
            onChange={scenarioChangeHandler}
          >
            <option value=""> - - - Make a selection - - - </option>
            <option value={TRAINING_SCENARIOS.join(",")}>Training</option>
            <option value={TESTING_SCENARIOS.join(",")}>Testing</option>
            <option value={ALL_SCENARIOS.join(",")}>Both</option>
          </select>
          <div>Scenarios: {JSON.stringify(scenarios)}</div>
        </div>
        <div className="col-6">
          <label htmlFor="conditions_menu" className="form-label">
            Condition(s)
          </label>
          <select
            id="conditions_menu"
            className="form-control"
            onChange={conditionsChangeHandler}
          >
            <option value=""> - - - Make a selection - - - </option>
            <option value="cond1">Condition 1</option>
            <option value="cond2">Condition 2</option>
            <option value="cond1,cond2">Both</option>
          </select>
        </div>
      </div>
      <div className="row">
        <div className="col-12">
          <button
            type="button"
            className="btn btn-primary"
            disabled={scenarios.length === 0 || conditions.length === 0}
            onClick={getClassCodeCondStudents}
          >
            Fetch student data
          </button>
        </div>
      </div>
      {matchingStudentData.length > 0 && (
        <div className="row">
          <div className="col-12">
            <hr />
            <p className="fs-3">
              Students matching classCode: "{CLASS_CODE}" conditions: "{conditions}"
            </p>
            <label htmlFor="students_menu" className="form-label">
              Select the students you wish to generate results for:
            </label>
            <select
              id="students_menu"
              multiple={true}
              size={10}
              className="form-control form-control-sm"
              onChange={studentsChangeHandler}
            >
              {matchingUsernames.map((userName, i) => (
                <option key={i} value={userName}>
                  {userName}
                </option>
              ))}
            </select>
          </div>
        </div>
      )}
      {selectedStudents.length > 0 && (
        <div className="row">
          <div className="col-12">
            <button
              type="button"
              className="btn btn-primary"
              onClick={handleRun}
            >
              Run
            </button>
          </div>
        </div>
      )}
      {run && (
        <GetScoring2
          studentMetaData={studentMetaData}
          obsID2analyzedLogsMap={analyzedLogsMap}
          scenariosMap={pubScensMap}
        />
      )}
    </div>
  );
}