
import React from "react"
import { Switch, Route} from 'react-router-dom';

import { useFirebaseProject, FirebaseContext} from "@meddbriefer/mdb-firebase"
import { AuthProvider } from "@meddbriefer/mdb-auth";
// import {
//   ObserverContext,
//   Scenario,
//   UnauthedLayout,
// } from "@meddbriefer/observer-ui";
// import Welcome from "./components/Welcome";
import Welcome2 from "./components/Welcome2"
//import { GetScoring } from "../components/GetScoring"

import { environment } from "./environments/environment";


import './styles.css';


export default function App() {
    const {auth, db} = useFirebaseProject(environment.firebaseProject)
    //const [observer, setObserver] = useState(null)

    return (
      <FirebaseContext.Provider
        value={{
          auth,
          db,
        }}
      >
          <AuthProvider>
            <Switch>
              <Route>
                <Welcome2 />
              </Route>
            </Switch>
          </AuthProvider>
      </FirebaseContext.Provider>
    );
}
